@import url(https://fonts.googleapis.com/css2?family=Rubik:ital,wght@1,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Jokes{
    display: flex;
    align-items: center;
    justify-content:center;
    border: 2px solid #eeeeee;
    font-weight: 400;
}

.Jokes-btn {
    display: flex;
    align-items: center;
    justify-items: center;
    width: 15%;
    margin-right: 1rem
}

.Jokes-text {
    width: 75%;
    font-size: 1.2rem
}

.Jokes-vote {
    width: 50px;
    height: 50px;
    line-height: 50px;
    border-radius: 50%;
    border: 3px solid red;
    text-align: center;
    font-size: 20px;
    font-weight: 300;
    box-shadow: 0 10px 20px rgba(0 0 0 / .2), 0 10px 12px rgba(0, 0, 0, .2);
}

.fas {
    cursor: pointer;
    font-size: 1.5rem;
    margin: 10px;
    transition: all .3s;
}



.fa-arrow-up:hover {
    animation-name: move-up;
    color: #8bc34a
}

.fa-arrow-down:hover{
    animation-name: move-down;
    color: #f44338
}

.fa-arrow-up:hover, .fa-arrow-down:hover {
    animation-duration: .7s;
    animation-timing-function: ease-out;
}

@keyframes move-up{
    0%,
    50%,
    100% { 
        transform: translateY(0px)
    }
    25%, 75% {
        transform: translateY(-3px);
    }
}

@keyframes move-down {
    0%,
    50%,
    100% { 
        transform: translateY(0px)
    }
    25%, 75% {
        transform: translateY(3px);
    }
}

.Jokes-smily{
    font-size: 2rem;
    margin-left: auto;
    box-shadow: 0 10px 20px rgba(0 0 0 / .2), 0 10px 12px rgba(0, 0, 0, .2);
    border-radius: 50%;
}

.Jokes-smily:hover{
    animation-name: spin;
    animation-duration: .7s;
    animation-timing-function: ease-in-out;
    animation-delay: 0s;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    30% {
        transform: rotate(20deg);
    }
    60% {
        transform: rotate(0deg);
    }
    80% {
        transform: rotate(10deg);
    }
    100% {
        transform: rotate(0deg);
    }
}
.JokeList{
    display: flex;
    height: 80%;
    width: 80%;
}

.JokeList-Sidebar {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: #9970cf;
    width: 30%;
    text-align: center;
    box-shadow: 0 19px 38px rgba(0 0 0 / .3), 0 15px 12px rgba(0, 0, 0, .1), inset 0 0 25px #7e57e2;
    z-index: 2;
}

.JokeList-title {
    font-size: 4rem;
    margin: 2rem;
    color: white;
    font-weight: 200;
    letter-spacing: .4rem;
}

.JokeList-title span {
    font-weight: 700;
    letter-spacing: 0;
}

.JokeList-Sidebar img{
    width: 50%;
    border-radius: 50%;
    box-shadow: 0 19px 38px rgba(0 0 0 / .3), 0 15px 12px rgba(0, 0, 0, .1);
}

.JokeList-Sidebar img:hover {
    animation-name: shake-slow;
    animation-duration: 5s;
    animation-timing-function: ease-in-out;
    animation-delay: 0s;
}

.JokeList-Jokes {
    height: 90%;
    background-color: white;
    align-self: center;
    width: 70%;
    overflow: scroll;
    box-shadow: 0 19px 38px rgba(0 0 0 / .3), 0 15px 12px rgba(0, 0, 0, .1);
}

.JokeList-spinner {
    color: #fff;
    text-align: center;
}

.JokeList-spinner .fas {
    
    font-size: 100px
}

.JokeList-getmore {
    font-size: 1.5rem;
    padding: 1rem 2rem;
    border-radius: 10px;
    margin: 2rem;
    outline: none;
    border: none;
    color: white;
    font-weight: 700;
    cursor: pointer;
    width: 50%;
    background: linear-gradient(
    135deg,
    rgba(179, 229, 252, 1) 0%,
    rgba(179, 229, 252, 1) 50%,
    rgba(249, 98, 162, 1) 50%,
    rgba(249, 98, 162, 1) 100%
  );
  word-spacing: .9rem;
  box-shadow: 0 19px 38px rgba(0 0 0 / .3), 0 15px 12px rgba(0, 0, 0, .1);
  transition: .3s cubic-bezier(0.075, 0.82, 0.165, 1);
}

.JokeList-getmore:hover {
    transform: translateY(-2px);
    box-shadow: 0 15px 30px rgba(0, 0, 0,  .25);
}

@keyframes shake-slow {
    0% {
      transform: translate(0px, 0px) rotate(0deg);
    }
    2% {
      transform: translate(7px, 5px) rotate(1.5deg);
    }
    4% {
      transform: translate(2px, 4px) rotate(1.5deg);
    }
    6% {
      transform: translate(5px, 1px) rotate(2.5deg);
    }
    8% {
      transform: translate(6px, 0px) rotate(-1.5deg);
    }
    10% {
      transform: translate(-5px, -5px) rotate(0.5deg);
    }
    12% {
      transform: translate(-6px, -6px) rotate(2.5deg);
    }
    14% {
      transform: translate(10px, 5px) rotate(3.5deg);
    }
    16% {
      transform: translate(1px, 10px) rotate(1.5deg);
    }
    18% {
      transform: translate(-7px, 1px) rotate(-0.5deg);
    }
    20% {
      transform: translate(-6px, 3px) rotate(-1.5deg);
    }
    22% {
      transform: translate(0px, 1px) rotate(1.5deg);
    }
    24% {
      transform: translate(-6px, 2px) rotate(3.5deg);
    }
    26% {
      transform: translate(4px, -4px) rotate(-2.5deg);
    }
    28% {
      transform: translate(8px, -1px) rotate(-2.5deg);
    }
    30% {
      transform: translate(-2px, 6px) rotate(1.5deg);
    }
    32% {
      transform: translate(-3px, 3px) rotate(-0.5deg);
    }
    34% {
      transform: translate(10px, -8px) rotate(0.5deg);
    }
    36% {
      transform: translate(-8px, -3px) rotate(3.5deg);
    }
    38% {
      transform: translate(0px, 8px) rotate(-0.5deg);
    }
    40% {
      transform: translate(-5px, 5px) rotate(2.5deg);
    }
    42% {
      transform: translate(6px, -1px) rotate(-0.5deg);
    }
    44% {
      transform: translate(5px, -8px) rotate(-2.5deg);
    }
    46% {
      transform: translate(-8px, 1px) rotate(3.5deg);
    }
    48% {
      transform: translate(6px, 9px) rotate(2.5deg);
    }
    50% {
      transform: translate(-4px, -2px) rotate(-0.5deg);
    }
    52% {
      transform: translate(3px, 9px) rotate(-2.5deg);
    }
    54% {
      transform: translate(0px, 7px) rotate(-1.5deg);
    }
    56% {
      transform: translate(-8px, 10px) rotate(0.5deg);
    }
    58% {
      transform: translate(-6px, 4px) rotate(2.5deg);
    }
    60% {
      transform: translate(-5px, -4px) rotate(1.5deg);
    }
    62% {
      transform: translate(10px, 10px) rotate(2.5deg);
    }
    64% {
      transform: translate(10px, -2px) rotate(0.5deg);
    }
    66% {
      transform: translate(-7px, 8px) rotate(-2.5deg);
    }
    68% {
      transform: translate(-1px, 6px) rotate(2.5deg);
    }
    70% {
      transform: translate(10px, 4px) rotate(2.5deg);
    }
    72% {
      transform: translate(8px, 10px) rotate(-0.5deg);
    }
    74% {
      transform: translate(0px, 2px) rotate(-0.5deg);
    }
    76% {
      transform: translate(8px, 2px) rotate(3.5deg);
    }
    78% {
      transform: translate(1px, -9px) rotate(3.5deg);
    }
    80% {
      transform: translate(1px, 2px) rotate(3.5deg);
    }
    82% {
      transform: translate(8px, -4px) rotate(-1.5deg);
    }
    84% {
      transform: translate(-2px, 7px) rotate(1.5deg);
    }
    86% {
      transform: translate(2px, 4px) rotate(1.5deg);
    }
    88% {
      transform: translate(-9px, -6px) rotate(-2.5deg);
    }
    90% {
      transform: translate(-1px, -6px) rotate(1.5deg);
    }
    92% {
      transform: translate(-6px, 6px) rotate(0.5deg);
    }
    94% {
      transform: translate(10px, -7px) rotate(2.5deg);
    }
    96% {
      transform: translate(-9px, 9px) rotate(0.5deg);
    }
    98% {
      transform: translate(0px, 1px) rotate(0.5deg);
    }
  }
.App {
  display: flex;
  /* text-align: center; */
  justify-content: center;
  align-items: center;
  height: 100vh;
  color: #78909c;
  background: linear-gradient(
    135deg,
    rgba(179, 229, 252, 1) 0%,
    rgba(179, 229, 252, 1) 50%,
    rgba(249, 98, 162, 1) 50%,
    rgba(249, 98, 162, 1) 100%
  );
  font-family: 'Rubik', sans-serif;
}
